* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.board {
  position: relative;
  width: 80%;
  margin: 100px auto;
  border-radius: 2rem;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #333;
  border-radius: 16px;
  background: #dde3f4;
  box-shadow: 14px 14px 28px #cbd1e0,
    -14px -14px 28px #eff5ff;
}

body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-thumb {
  background-color: #1c3672;
  border-radius: 4px;
}

body::-webkit-scrollbar-track {
  background-color: #a9b6d6;
  border-radius: 4px;
}

header {
  width: 100%;
  border-bottom: 2px solid white;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  padding: 1rem;
  font-size: 3rem;
  letter-spacing: 2px;
  color: white;
  text-shadow: 1px 1px 3px rgba(45, 103, 239, 0.5);
  background-color: #dde3f4;
}


.list {
  width: 80%;
  padding: 1rem;
  border-bottom: 1px solid white;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom : 1rem
}

.listname {
  font-size: large;
  padding: 0.75rem 1.5rem;
  position: relative;
  font-weight: bolder;
  cursor: pointer;
}

.delete {
  margin-left: 20px;
  width: 30px;
  height: 30px;
  border-style: none;
  border-radius: 50%;
  background: #f5f8ff;
  box-shadow: 5px 5px 3px #e1e4eb,
    -3px -3px 6px #ffffff;
  cursor: pointer;
  color: lightslategray;
  font-weight: bolder;
}

.delete:hover {
  background: linear-gradient(145deg, #dddfe6, #ffffff);
  box-shadow: 5px 5px 6px #e1e4eb,
    -5px -5px 6px #ffffff;
}

.listcontents {
  width: calc(100% - 70px);
  margin-left: 20px;
  color: white;
  text-shadow: 1px 1px 3px rgba(15, 31, 68, 0.3);
  background-color: rgba(15, 31, 68, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
}

.date {
  width: calc(100% - 100px);
  font-size: 0.75rem;
  padding-left: 2rem;
  margin: 1rem 0;
}

.good {
  width: 100px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 0.25rem;
  font-size: smaller;
}

.good span {
  cursor: pointer;
  transition: .3s;
  font-size: large;
}

.good span:hover {
  transform: scale(1.1);
}


.input {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem 0;
}

.input div {
  width: 100%;
}

.input div span {
  padding: 0.5rem 1rem;
  color: white;
  font-size: larger;
  text-shadow: 0px 0px 5px rgba(15, 31, 68, 0.5);
}

.input div input {
  width: 80%;
  border-radius: 1rem;
  background: linear-gradient(145deg, #ffffff, rgba(45, 103, 239, 0.1));
  box-shadow: 9px 9px 18px rgba(45, 103, 239, 0.3),
    -9px -9px 18px #ffffff;
  border-style: none;
  padding: 0.5rem;
}

.input div input::placeholder {
  color : lightslategray;
  font-size: 0.75rem;
}

.input div input:hover,
.input div input:focus {
  outline: none;
  background: linear-gradient(145deg, rgba(45, 103, 239, 0.3), #ffffff);
  box-shadow: 9px 9px 18px rgba(45, 103, 239, 0.3),
    -9px -9px 18px #ffffff;
}

.input-title input {
  margin-bottom: 1rem;
  border-radius: 8px !important;
}

.input-content input {
  height: 100px;
}

.submit-btn {
  border-style: none;
  padding: 1rem 2rem;
  margin: 1rem 0;
  border-radius: 12px;
  background: #e9e7f4;
  box-shadow: 4px 4px 9px #c8c7d2,
    -4px -4px 9px #ffffff;
  cursor: pointer;
}

.submit-btn:hover {
  background: #e9e7f4;
  box-shadow: inset 4px 4px 9px #c8c7d2,
    inset -4px -4px 9px #ffffff;
}


.modal {
  background-color: #24509e;
  position: absolute;
  height: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: 100;
}

.modal h4 {
  font-weight: bolder;
  font-size: 2rem;
  padding: 1rem 0;
  border-bottom: 1px solid white;
  width: 80%;
  text-align: center;
}

.modal p {
  padding: 1rem 0;
}

.modal p:last-of-type {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}

.modal .close-btn {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 1rem;
  top: 0.75rem;
  cursor: pointer;
}

.modal .close-btn span {
  position: absolute;
  width: 100%;
  height: 3px;
  border-radius: 1.5px;
  top : 50%;
  background-color: white;
}

.modal .close-btn span:nth-of-type(1) {
  transform: translateY(-50%) rotate(45deg);
}

.modal .close-btn span:nth-of-type(2) {
  transform: translateY(-50%) rotate(-45deg);
}
